import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmControl 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-control', VmControl);
  

export interface ControlProps extends JSX.VmControl, HTMLAttributes<HTMLVmControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmControlElement, ControlProps>(
  'vm-control',
  new Set(['keys','identifier','hidden','label','menu','expanded','pressed','isTouch']),
);
  